interface ICreateClassName {
	[key: string]: string;
}

export function createClassName(className: ICreateClassName) {
	return (...str: string[]) => {
		let list: string[] = [];
		str.forEach((item) => {
			if (globalName) {
				if (Object.keys(globalName).includes(item)) {
					list.push(globalName[item]);
				}
			}
			if (str.includes(item)) {
				list.push(className[item]);
			}
			if (Object.values(className).includes(item)) {
				list.push(item);
			}

			//TODO –†–µ—à–∏—Ç—å –∫–∞–∫ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞—Ç—å —Å—Ç–∏–ª–∏, –∫–æ—Ç–æ—Ä—ã–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç –ø–æ –Ω–∞–∑–≤–∞–Ω–∏—é
			//NOTE –ü–æ–∫–∞ –≤—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–µ —Å—Ç–∏–ª–∏ –≤—Å—Ç–∞–≤–ª—è—é—Ç—Å—è –∑–∞—Ä–∞–Ω–µ–µ, —á–µ–º –ø–µ—Ä–µ—á–µ—Ä–∫–∏–≤–∞—é—Ç –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –≥–ª–æ–±–∞–ª—å–Ω—ã—Ö —Å—Ç–∏–ª–µ–π
		});
		return joinWithoutDouble(list);
	};
}

export function registryGlobalName(className: ICreateClassName): void {
	if (className) {
		if (globalName) {
			console.error('–£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω –æ–¥–∏–Ω –≥–ª–æ–±–∞–ª—å–Ω—ã–π —Å—Ç–∏–ª—å');
		} else {
			globalName = className;
		}
	}
}

export function getGlobalName() {
	return (...str: string[]) => {
		let list: string[] = [];
		str.forEach((item) => {
			if (globalName) {
				if (Object.keys(globalName).includes(item)) {
					list.push(globalName[item]);
				}
			}
		});
		return joinWithoutDouble(list);
	};
}
